* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  font-family: sans-serif;
  font-weight: 600;
}

body {
  background-color: #3b3b4f;
  color: white;
}
body .container {
  text-align: center;
  width: 40%;
  margin: 15% auto 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
}
body .container .phone {
  background-color: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  padding: 15px;
  border-radius: 10px;
}
body .container .phone #camera {
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: #dfdfe2;
}
body .container .phone #screen {
  background-color: #dfdfe2;
  color: black;
  padding: 10px;
  height: 300px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
body .container .phone #screen label,
body .container .phone #screen input {
  display: block;
}
body .container .phone #screen input {
  height: 30px;
  border-radius: 5px;
  border: 1px solid black;
  padding-inline-start: 10px;
  outline: none;
}
body .container .phone .buttons-container {
  display: flex;
  justify-content: space-around;
  width: 100%;
}
body .container .phone .buttons-container button {
  height: 30px;
  width: 80px;
  font-size: 1.25rem;
  cursor: pointer;
  background-image: linear-gradient(#ffffff, #928d86);
  color: #0a0a23;
  border: 2px solid white;
}